*{
    padding: 0;
    margin: 0;
}
.nav-Container{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    align-items: center;
}

.MenuLinks > ul {
    display: flex;
    padding: 20px;
    gap: 50px;
}

.MenuLinks > ul > li{
    list-style: none;
    font-size: 20px;
}

.search{
    margin: 15px;
    margin-left: 75px;
}

.top-heading{
    display: flex;
    gap: 20px;
    align-items: center;
    margin-left: 75px;
}

.filter-btn, .search-btn{
    padding: 0px, 0px;
    width: 75px;
    height: 25px;
    margin-left: 5px;
}

.restorant-container{
    display: flex;
    flex-wrap: wrap;
    margin-left: 60px;
}

.restorant-card{
    margin-top: 15px;
    margin-left: 15px;
    width: 220px;
    border-radius: 10px;
    cursor: pointer;
    padding-bottom: 10px;
    
    overflow: hidden;
    
}

.restorant-card:hover{
    border: 1px solid black;
}

.card-seound-part{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding-left: 10px;
    padding-right: 5px;

}

.res-title-time{
    display: flex;
    align-items: center;
    gap: 5px;
}

.card-img{
    width: 100%;
    height: 151px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    
}

.explore-price{
    display: flex;
    gap: 20px;
    float: left;
   
    
}

.view-more-btn{
    padding: 0px 15px;
}

.shimmer-container{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding-top: 20px;
    padding-left: 20px;
    margin-top: 50px;
    margin-left: 50px;
}
.shimmer-card{
    
    background-color: #eeeded;
    height: 280px;
    width: 220px;
    border-radius: 10px;
}

.user-card{
    border: 2px solid black;
    padding: 10px;
}